.details {
  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 48px;
    }
  }
}

.detail {
  &__photos {
    @include pageGrid;
  }

  &__link {
    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    transition-duration: $duration;
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin-bottom: 16px;

    &:hover {
      transform: scale(1.02);
    }
  }

  &__title {
    margin: 16px 0 8px;
    color: $header-title-color;
    font-size: 24px;
    font-weight: 700;
    line-height: 140%;
  }

  &__description {
    margin: 0;
    color: $header-title-color;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    @include onDesktop {
      grid-column: span 8;
    }
  }
}
